/* Full-screen container with gradient background */
.adv-booking-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100vw;
    height: 100vh;
    position: relative;
    background: linear-gradient(to bottom, #007bff, #ffffff);
    box-sizing: border-box;
    padding-top: 0;
    margin: 0;
  }
  
  /* Top right logo for navigation */
  .top-logo {
    position: absolute;
    top: 20px;
    right: 20px;
    width: 50px;
    cursor: pointer;
  }
  
  /* Advance Booking Content */
  .adv-booking-content {
    background-color: rgba(255, 255, 255, 0.9); /* Semi-transparent background */
    margin-top: 300px;
    padding: 30px;
    border-radius: 10px;
    text-align: center;
    width: 90%;
    max-width: 400px;
    z-index: 1;
    box-sizing: border-box;
  }
  
  .adv-booking-content h2 {
    margin: 0;
    font-size: 1.5rem;
    color: #333;
  }
  
  /* Form group styling */
  .form-group {
    margin-bottom: 15px;
    text-align: left;
  }
  
  .form-group label {
    font-size: 1rem;
    color: #333;
    margin-bottom: 5px;
    display: block;
  }
  
  .form-group input[type="text"],
  .form-group input[type="number"] {
    width: 100%;
    padding: 10px;
    font-size: 1rem;
    border: 1px solid #cccccc;
    border-radius: 5px;
    box-sizing: border-box;
  }
  
  /* Number of persons selector */
  .num-persons {
    display: flex;
    align-items: center;
    gap: 10px;
  }
  
  .num-persons button {
    padding: 5px 10px;
    font-size: 1.2rem;
    cursor: pointer;
    border: none;
    background-color: #007bff;
    color: #fff;
    border-radius: 5px;
  }
  
  .num-persons button:disabled {
    background-color: #cccccc;
    cursor: not-allowed;
  }
  
  /* Luggage radio buttons */
  .luggage-options {
    display: flex;
    gap: 20px;
  }
  
  .luggage-options label {
    font-size: 1rem;
    display: flex;
    align-items: center;
    gap: 8px;
  }
  
  /* Send to Driver Button */
.send-button {
    padding: 10px 20px;
    font-size: 1rem;
    color: #fff;
    background-color: #6c757d;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 10px;
  }
  
  .send-button.accepted {
    background-color: green;
  }
  
  .send-button:disabled {
    background-color: #cccccc;
    cursor: not-allowed;
  }
  
 /* Proceed to Pay button */
.pay-button {
    margin-top: 15px;
    padding: 10px 20px;
    font-size: 1rem;
    color: #fff;
    background-color: #007bff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }

  .pay-button:disabled {
    background-color: #cccccc;
    cursor: not-allowed;
  }
  
  .pay-button:hover:not(:disabled) {
    background-color: #0056b3;
  }

/* Cancel Ride Button */
.cancel-button {
    margin-top: 10px;
    padding: 10px 20px;
    font-size: 1rem;
    color: #fff;
    background-color: #dc3545;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  